const Sort = ({ onSort }) => {

  return (
    <div className="d-flex align-items-center">
      <label style={{ marginRight: '10px' }}>Show:</label>
      <select className="form-select ml-3" onChange={e => onSort(e.target.value)}>
        <option>All</option>
        <option>Active</option>
        <option>Completed</option>
      </select>
    </div>
  )
}

export default Sort