import { useState } from 'react'

const Add = ({ onAdd }) => {
  const [text, setNewTodo] = useState('')
  const [isDone] = useState(false)
  const [hide] = useState(false)

  const takeNewTodo = (e) => {
    e.preventDefault()

    text && onAdd({ text, isDone, hide })

    setNewTodo('')
  }

  return (
    <form className="input-group" onSubmit={takeNewTodo}>
      <input className="form-control" type="text" placeholder="New Todo"
        value={text} onChange={e => setNewTodo(e.target.value)} />
      <input className="add-btn btn" type="submit" value="Add" />
    </form>
  )
}

export default Add