import { useState } from 'react'
import Header from './components/Header'
import Add from './components/Add'
import Todos from './components/Todos'
import Settings from './components/Settings'

const App = () => {
	let [todos, setTodos] = useState([
		{
			id: 1,
			text: 'Meeting with colleagues',
			isDone: true,
			hide: false
		},
		{
			id: 2,
			text: 'Going out with friends',
			isDone: false,
			hide: false
		},
		{
			id: 3,
			text: 'Saturday game clubbing',
			isDone: true,
			hide: false
		}
	])

	// Add Todo
	const addTodo = (todo) => {
		const id = Math.floor(Math.random() * 1000) + 1
		const newTodoTask = { id, ...todo }
		setTodos([...todos, newTodoTask])
	}

	// Delete Todo
	const deleteTodo = (id) => {
		setTodos(todos.filter(todo => todo.id !== id))
	}

	// Toggle Todo
	const toggleTodo = (id) => {
		setTodos(todos.map(todo => todo.id === id ? { ...todo, isDone: !todo.isDone } : todo))
	}

	// Sort Todos
	const todosSort = (value) => {
		switch (value) {
			case 'Active':
				setTodos(todos.map(todo => todo.isDone === true || todo.hide === true ? { ...todo, hide: !todo.hide } : todo))
				break
			case 'Completed':
				setTodos(todos.map(todo => todo.isDone === false || todo.hide === true ? { ...todo, hide: !todo.hide } : todo))
				break
			case 'All':
				setTodos(todos.map(todo => todo.hide === true ? { ...todo, hide: false } : todo))
				break
			default:
				console.log('Nothing')
				break
		}
	}

	// Clear All
	const clearAll = () => {
		setTodos([])
	}

	return (
		<div className="container">
			<div className="row">
				<div className="col">
					<Header title="CheckList" />
					<Add onAdd={addTodo} />
					{todos.length > 0 ? (
						<Todos todosList={todos} onDelete={deleteTodo} onToggle={toggleTodo} />
					) : (
							<p className="text-center my-4">type in "New Todo" list and click add...</p>
						)}
					<Settings clearAll={clearAll} todosSort={todos} onSort={todosSort} />
				</div>
			</div>
		</div>
	)
}

export default App